.popup {
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0,0,0, 0.5);
  justify-content: center;
  align-items: center;
  font-family: 'Inter', Verdana, Arial, Helvetica, sans-serif;
  visibility: hidden;
  opacity: 0;
  transition: visibility .7s, opacity .7s linear;
}